.rcfm-form-fieldset-previewer
  background: $form-fieldset-previewer-background
  border-radius: $form-fieldset-previewer-border-radius
  box-shadow: $form-fieldset-previewer-box-shadow

.rcfm-form-fieldset-previewer-header
  overflow: hidden
  border-radius: $form-fieldset-previewer-border-radius
  border-bottom-left-radius: 0
  border-bottom-right-radius: 0

  .rcfm-form-member-previewer-actions>[class*=rcfm-].rcfm-form-member-previewer-edit
    margin-left: auto

.rcfm-form-fieldset-previewer-children
  max-width: $form-fieldset-previewer-children-max-width
  margin-left: auto
  margin-right: auto
  padding: $form-fieldset-previewer-children-padding

@media (min-width: $width-breakpoint-small)

  .rcfm-form-fieldset-previewer-children

    padding: $form-fieldset-previewer-children-padding-medium

@media (min-width: $width-breakpoint-medium)

  .rcfm-form-fieldset-previewer-children

    padding: $form-fieldset-previewer-children-padding-large

.rcfm-form-fieldset-previewer-child-picker
  position: relative
  display: flex
  justify-content: center
  align-items: center
  height: $form-fieldset-previewer-child-picker-height
  margin: $form-fieldset-previewer-child-picker-margin

.rcfm-form-fieldset-previewer-child-picker-trigger
  position: absolute
  top: 0
  right: 0
  bottom: 0
  left: 0
  margin: auto

.rcfm-form-fieldset-previewer-child-picker-pannel-wrapper
  position: fixed
  bottom: 0
  left: 0
  z-index: $z-index-highest
  display: flex
  flex-direction: column
  justify-content: flex-end
  height: 100%
  width: 100%
  padding: $form-fieldset-previewer-child-picker-pannel-wrapper-padding
  background: $form-fieldset-previewer-child-picker-pannel-wrapper-background

@media (min-width: $width-breakpoint-small)

  .rcfm-form-fieldset-previewer-child-picker-pannel-wrapper

    position: initial
    display: block
    width: auto
    height: auto
    background: none

.rcfm-form-fieldset-previewer-child-picker-pannel
  position: relative
  z-index: $form-fieldset-previewer-child-picker-pannel-z-index
  max-height: $form-fieldset-previewer-child-picker-pannel-max-height
  padding: $form-fieldset-previewer-child-picker-pannel-padding
  background: $form-fieldset-previewer-child-picker-pannel-background
  border-radius: $form-fieldset-previewer-child-picker-pannel-border-radius
  box-shadow: $form-fieldset-previewer-child-picker-pannel-box-shadow
  overflow: auto
  -webkit-overflow-scrollilng: touch

.rcfm-form-fieldset-previewer-child-picker-pannel-title
  position: sticky
  top: 0
  left: 0
  z-index: $z-index-high
  padding: $form-fieldset-previewer-child-picker-pannel-title-padding
  font-size: $form-fieldset-previewer-child-picker-pannel-title-font-size
  line-height: 1
  overflow: hidden
  white-space: nowrap
  text-overflow: ellipsis
  font-weight: $form-fieldset-previewer-child-picker-pannel-title-font-weight
  color: $form-fieldset-previewer-child-picker-pannel-title-color
  background: $form-fieldset-previewer-child-picker-pannel-title-background
