.rcfm-form-fieldset
  max-width: $form-fieldset-max-width

.rcfm-form-fieldset-header
  margin: $form-fieldset-header-margin
  padding: $form-fieldset-header-padding
  background: $form-fieldset-header-background

.rcfm-form-fieldset-header-title
  margin: $form-fieldset-header-title-margin
  font-size: $form-fieldset-header-title-font-size
  line-height: $form-fieldset-header-title-line-height
  font-weight: $form-fieldset-header-title-font-weight
  color: $form-fieldset-header-title-color

.rcfm-form-fieldset-header-description
  margin: $form-fieldset-header-description-margin
  font-size: $form-fieldset-header-description-font-size
  line-height: $form-fieldset-header-description-line-height
  color: $form-fieldset-header-description-color
