.rcfm-form-member-previewer-fallback
  overflow: hidden
  border-radius: $form-member-previewer-fallback-border-radius
  box-shadow: $form-member-previewer-fallback-box-shadow

.rcfm-form-member-previewer-fallback-member-wrapper
  display: block
  margin: 0
  padding: $form-member-previewer-fallback-member-wrapper-padding
  border: none
  pointer-events: none
  background: $form-member-previewer-fallback-member-wrapper-background

.rcfm-form-member-previewer-actions
  position: relative
  display: flex
  justify-content: space-between
  align-items: center
  margin: $form-member-previewer-actions-margin
  padding: $form-member-previewer-actions-padding
  background: $form-member-previewer-actions-background

.rcfm-form-member-previewer-actions > [class*=rcfm-]
  margin: $form-member-previewer-actions-elements-margin

  &:first-child
    margin-left: 0

  &:last-child
    margin-right: 0

  &.rcfm-form-member-previewer-delete
    margin-right: auto
