.rcfm-view-form-builder
  position: relative
  height: 100%

.rcfm-view-form-builder > .rcfm-alert-error
  position: absolute
  bottom: 0
  left: 0
  right: 0
  margin: $form-builder-alert-margin

@media (max-width: $width-breakpoint-small)

  .rcfm-view-form-builder-editor
    position: fixed
    bottom: 0
    left: 0
    display: flex
    flex-direction: column
    justify-content: flex-end
    height: 100%
    width: 100%
    padding: $form-builder-editor-padding
    background: $form-builder-editor-background

  .rcfm-view-form-builder-editor-content
    height: 61.8%

  .rcfm-form-member-editor-fallback
    height: 100%
    overflow: auto
    -webkit-overflow-scrolling: touch
    border-radius: $form-builder-editor-border-radius
    box-shadow: $form-builder-editor-box-shadow

@media (min-width: $width-breakpoint-small)

  .rcfm-view-form-builder
    display: flex
    justify-content: space-between
    align-items: flex-start
    position: relative

  .rcfm-view-form-builder-previewer,
  .rcfm-view-form-builder-editor
    flex: 1 1 100%
    height: 100%
    overflow: auto
    overflow: overlay
    -webkit-overflow-scrolling: touch

  .rcfm-view-form-builder-previewer-content
    position: relative
    min-height: 100%
    padding: $form-builder-previewer-padding

    &:after
      content: ''
      position: absolute
      top: 0
      right: 0
      height: 100%
      width: 100%
      display: none
      background: $form-builder-previewer-background

    & .rcfm-form-fieldset-previewer
      border-radius: $form-builder-previewer-border-radius
      box-shadow: $form-builder-previewer-box-shadow

  .rcfm-view-form-builder-editor-content
    position: relative
    min-height: 100%
    padding: $form-builder-editor-padding

    &:after
      content: ''
      position: absolute
      top: 0
      right: 0
      height: 100%
      width: 100%
      display: none
      background: $form-builder-editor-background

    & .rcfm-form-member-editor-fallback
      position: relative
      z-index: $z-index-high
      overflow: hidden
      border-radius: $form-builder-editor-border-radius
      box-shadow: $form-builder-editor-box-shadow

  .rcfm-view-form-builder.rcfm-view-form-builder-editor-active

    & .rcfm-view-form-builder-previewer-content:after,
    & .rcfm-view-form-builder-editor-content:after
      display: block

  .rcfm-form-member-previewer-fallback-highlight,
  .rcfm-form-fieldset-previewer-highlight .rcfm-form-fieldset-header
    position: relative
    z-index: $z-index-high
