.rcfm-highlight,
.rcfm-highlight-info,
.rcfm-highlight-success,
.rcfm-highlight-error,
.rcfm-highlight-warning
  display: block
  padding: $highlight-on-padding
  overflow: hidden
  border-radius: $highlight-border-radius
  box-shadow: $highlight-on-box-shadow

.rcfm-highlight-info
  background: $highlight-info-on-background

.rcfm-highlight-success
  background: $highlight-success-on-background

.rcfm-highlight-error
  background: $highlight-error-on-background

.rcfm-highlight-warning
  background: $highlight-warning-on-background

[class*=rcfm-highlight].rcfm-highlight-off
  padding: $highlight-off-padding
  background: none
  box-shadow: $highlight-off-box-shadow

[class*=rcfm-highlight] > .rcfm-highlight-hint
  margin: $highlight-hint-margin
