  margin: $form-member-editor-prop-options-item-element-margin

.rcfm-form-member-editor-prop-options-item-elements > .rcfm-btngroup-x
  flex: 0 0 auto

.rcfm-form-member-editor-prop-options-item-elements > .rcfm-fmlabel
  flex: 0 0 auto
  margin: $form-member-editor-prop-options-item-element-label-margin

.rcfm-form-member-editor-prop-options-item-elements > [class*=rcfm]:first-child
  margin-left: 0

.rcfm-form-member-editor-prop-options-item-elements > [class*=rcfm]:last-child
