.rcfm-form-member-editor-condition-branch-title
  margin: $form-member-editor-condition-branch-title-margin
  font-size: $form-member-editor-condition-branch-title-font-size
  font-weight: $form-member-editor-condition-branch-title-font-weight
  overflow: hidden
  line-height: 1
  white-space: nowrap
  text-overflow: ellipsis

.rcfm-form-member-editor-condition-when-branch

  & > .rcfm-btngroup-x
    margin: $form-member-editor-condition-when-branch-button-group-margin

  & > .rcfm-highlight-error
    margin: $form-member-editor-condition-when-branch-rule-margin

    &:first-child
      margin-top: 0

    &:last-child
      margin-bottom: 0

.rcfm-form-member-editor-condition-when-branch-rule,
.rcfm-form-member-editor-condition-when-branch-rule > .rcfm-highlight-error
  margin: $form-member-editor-condition-when-branch-rule-elements-margin

  &:first-child
    margin-top: 0

  &:last-child
    margin-bottom: 0

.rcfm-form-member-editor-condition-when-branch-rule-elements
  display: flex
  justify-content: space-between
  align-items: center

  & > [class*=rcfm-]
    flex: 1 1 auto
    margin: $form-member-editor-condition-when-branch-rule-element-margin

  & > .rcfm-form-member-editor-condition-when-branch-rule-logic
    flex: 0 0 $form-member-editor-condition-when-branch-rule-logic-width

  & > .rcfm-btngroup-x
    flex: 0 0 auto

  & > [class*=rcfm-]:first-child
    margin-left: 0

  & > [class*=rcfm-]:last-child
    margin-right: 0
